@import "../../Styles/main.scss";

.Footer {
  margin-top: 25px;
  background-color: var(--color-nav);
  color: var(--text-color);

  padding: 20px 10px;
  text-align: center;

  .footer {
    max-width: 1200px;
    margin: 0 auto;

    .footer-content {
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between;
      align-items: center;

      .contact-info {
        flex: 1;
        padding: 10px;

        p {
          margin: 0;
          font-size: 1rem;
          color: var(--text-color);
          width: fit-content;
          padding-bottom: 3px;
          border-bottom: var(--secondary-color) solid;
          cursor: pointer;

          .email-link {
            color: var(--primary-color);
            text-decoration: none;
            margin: 0 5px;

            &:hover {
              text-decoration: underline;
            }
          }

          .copy-button {
            background-color: transparent;
            border: none;
            cursor: pointer;
            color: #42aff8;
            font-size: 16px;
            padding: 5px;
            transition: color 0.3s;

            &:hover {
              color: var(--primary-color);
            }

            &:focus {
              outline: none;
            }
          }
        }
      }

      .social-links {
        list-style: none;
        padding: 0;
        display: flex;
        justify-content: center;
        gap: 15px;
        padding: 10px;

        .social-item {
          background-color: var(--color-background);
          border-radius: 50%;
          width: 40px;
          height: 40px;
          display: flex;
          align-items: center;
          justify-content: center;
          transition: background-color 0.3s;



          &:hover {
            background-color: var(--color-primario-light);
          }

          .social-logo {
            display: flex;
            width: 24px;
            height: 24px;
          }
        }
      }

      .quick-links {
        flex: 1;
        padding: 10px;

        button {
          background: none;
          border: none;
          cursor: pointer;
          font: inherit;
          color: var(--color-secundario);
          text-decoration: none;
          margin: 0 10px;
          font-size: 1rem;
          border-radius: 10px;
          padding: 5px;

          &:hover {
            color: var(--secondary-color-light);
            background-color: var(--color-primario);
            text-decoration: underline;
          }
        }
      }
    }
  }

  // Media queries para responsividad
  @media (max-width: 768px) {
    .footer-content {
      flex-direction: column;

      .contact-info,
      .social-links,
      .quick-links {
        text-align: center;
        margin-bottom: 10px;
      }

      .social-links {
        justify-content: center;

        .social-item {
          width: 30px;
          height: 30px;

          .social-logo {
            width: 20px;
            height: 20px;
          }
        }
      }
    }
  }
}

[data-theme='dark'] {

  .GitHub,
  .Twitter {
    .social-logo {
      filter: invert(1);
    }
  }
}